"use client";

import { useState, useRef, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "../components/common/Card";
import { Input } from "../components/common/Input";
import { Button } from "../components/common/Buttons";
import { ScrollArea } from "../components/common/ScrollArea";
import { Send, Bot, User, X } from "lucide-react";
import { supabase } from "../../app/lib/supabase-client";

interface Message { 
  id: string; 
  role: "user" | "assistant"; 
  content: string; 
  timestamp: string;
}

export function TodoChatbot({ onClose }: { onClose: () => void }) {
  const [messages, setMessages] = useState<Message[]>([
    { 
      id: "1", 
      role: "assistant", 
      content: "Hi! Ask me anything about your todos.", 
      timestamp: new Date().toISOString() 
    }
  ]);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim() || isLoading) return;
  
    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: input,
      timestamp: new Date().toISOString(),
    };
  
    setMessages(prev => [...prev, userMessage]);
    setInput("");
    setIsLoading(true);
  
    try {
      // ðŸ”¹ Try getting Supabase session (optional)
      const { data: { session } } = await supabase.auth.getSession();
  
      const headers: Record<string, string> = {
        "Content-Type": "application/json",
      };
      if (session?.access_token) {
        headers["Authorization"] = `Bearer ${session.access_token}`;
      }
  
      // ðŸ”¹ Call the chatbot function
      const { data, error } = await supabase.functions.invoke("chatbot", {
        headers,
        body: { query: userMessage.content },
      });
  
      if (error) throw error;
  
      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: data?.answer || "I couldn't generate a response.",
        timestamp: new Date().toISOString(),
      };
  
      setMessages(prev => [...prev, assistantMessage]);
    } catch (err) {
      console.error("Chatbot error:", err);
      setMessages(prev => [
        ...prev,
        {
          id: (Date.now() + 1).toString(),
          role: "assistant",
          content:
            " Iâ€™m running in mock mode. Try questions like:\n- What are my total todos?\n- When did I complete groceries?\n- How many deleted todos?",
          timestamp: new Date().toISOString(),
        },
      ]);
    } finally {
      setIsLoading(false);
    }
  };
  

  return (
    <Card className="h-[calc(100vh-8rem)] flex flex-col">
      <CardHeader className="border-b flex justify-between items-center">
        <div>
          <CardTitle className="flex items-center gap-2">
            <Bot className="h-5 w-5 text-primary" /> Todo Assistant
          </CardTitle>
          <CardDescription>Ask me anything about your tasks</CardDescription>
        </div>
        <Button variant="primary" size="sm" onClick={onClose}>
          <X className="h-4 w-4" />
        </Button>
      </CardHeader>
      <CardContent className="flex-1 flex flex-col p-0">
        <ScrollArea className="flex-1 p-4" ref={scrollRef}>
          <div className="space-y-4">
            {messages.map(msg => (
              <div key={msg.id} className={`flex gap-3 ${msg.role === "user" ? "justify-end" : ""}`}>
                {msg.role === "assistant" && (
                  <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center">
                    <Bot className="h-4 w-4 text-primary-foreground"/>
                  </div>
                )}
                <div className={`rounded-lg px-4 py-2 max-w-[80%] ${
                  msg.role === "user" 
                    ? "bg-primary text-primary-foreground" 
                    : "bg-muted text-foreground border border-border"
                }`}>
                  <p className="text-sm whitespace-pre-wrap">{msg.content}</p>
                </div>
                {msg.role === "user" && (
                  <div className="h-8 w-8 rounded-full bg-accent flex items-center justify-center">
                    <User className="h-4 w-4 text-accent-foreground"/>
                  </div>
                )}
              </div>
            ))}
            {isLoading && (
              <div className="flex gap-3">
                <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center">
                  <Bot className="h-4 w-4 text-primary-foreground"/>
                </div>
                <div className="rounded-lg px-4 py-2 bg-muted text-foreground border border-border">
                  <p className="text-sm">Thinking...</p>
                </div>
              </div>
            )}
          </div>
        </ScrollArea>
        <div className="border-t p-4 flex gap-2">
          <Input 
            placeholder="Ask about your todos..." 
            value={input} 
            onChange={e => setInput(e.target.value)} 
            onKeyDown={e => e.key === "Enter" && handleSend()}
            disabled={isLoading}
          />
          <Button onClick={handleSend} disabled={isLoading}>
            <Send className="h-4 w-4"/>
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}
